<template>
	<view class="content">
		<view class="main-area">
			<!-- 类别筛选 -->
			<view class="u-tabs-area">
				<u-tabs :list="categoryList" @click="changeCategory" :current="categoryCurrent"></u-tabs>
			</view>
			
			<!-- 搜索框 -->
			<view class="search-area">
				<u-search v-model="params.keywords" @custom="search" @search="search" :shape="'round'" @clear="clear" 
					:clearabled="false" :show-action="false" :input-align="'left'" placeholder="输入车辆的名称或品牌"></u-search>
			</view>
		</view>
		
		<!-- 品牌索引列表 -->
		<view class="index-list-area">
			<!-- 字母、品牌索引列 -->
			<u-index-list :indexList="letterList" :customNavHeight="50">
				<template v-for="(item, index) in brandArr">
					<!-- 品牌 -->
					<u-index-item :key="index" v-if="index===0">
						<!-- #ifdef H5 -->
						<u-index-anchor v-show="false" :bgColor="'#f7f8fc'" :height="20" :text="letterList[index]"></u-index-anchor>
						<!-- #endif -->
						<view class="grid-box">
							<u-grid :col="5">
								<u-grid-item v-for="(gridItem, gridIndex) in gridList" :key="gridIndex">
									<img :src="gridItem.icon" class="grid-image"></img>
									<text class="grid-text">{{ gridItem.name }}</text>
								</u-grid-item>
							</u-grid>
							<u-grid :col="4">
								<u-grid-item v-for="(condGridItem, condGridIndex) in condGridList" :key="condGridIndex">
									<view class="grid-cond">{{condGridItem.name}}</view>
								</u-grid-item>
							</u-grid>
						</view>
					</u-index-item>
					<!-- 字母 -->
					<u-index-item :key="index" v-if="index>0">
						<u-index-anchor :bgColor="'#f7f8fc'" :height="20" :text="letterList[index]"></u-index-anchor>
						<view class="list" v-for="(item1, index1) in item" :key="index1">
							<view class="index_item">
								<image class="index_item_img" :src="item1.icon"></image>
								<text class="index_item_brand">{{item1.name}}</text>
							</view>
						</view>
					</u-index-item>
				</template>
			</u-index-list>
		</view>
	</view>
</template>

<script>
const letterList = () => {
	const letterList = []
	const charCodeOfA = 'A'.charCodeAt(0)
	letterList.push("选")
	for (let i = 0; i < 26; i++) {
		letterList.push(String.fromCharCode(charCodeOfA + i))
	}
	return letterList
}
export default {
	components: {  },
