					{ name: "北汽新能源", icon: 'http://124.223.108.78:8888/group1/M00/00/00/CgAEB2JeakiATrIVAAAEfNg5rZU744.jpg' },
					{ name: "北汽制造", icon: 'http://124.223.108.78:8888/group1/M00/00/00/CgAEB2JealSAbiqKAAACaiUgVs4246.jpg' },
				],
			},
			//参数
			params:{ 
				keywords: '',
			},
		}
	},
	computed: {
		brandArr() {
			return this.letterList.map(item => {
				let arr = [];
				let tempList = this.brandList[item];
				if(tempList){
					for(let i = 0; i< tempList.length; i++){
						let tempItem = tempList[i];
						arr.push({name: tempItem.name, icon: tempItem.icon});
					}
				}else{
					tempList = this.brandList['A'];
					for(let i = 0; i< tempList.length; i++){
						let tempItem = tempList[i];
						arr.push({name: tempItem.name, icon: tempItem.icon});
					}
				}
				return arr;
			})
		}
	},
	onLoad() {
		this.loadData();
	},
	methods: {
		//加载数据
		async loadData(){
			
		},
		//改变类别
		changeCategory(item){
			this.categoryCurrent = item.index;
		},
		search(){
			
		},
		clear() {
			this.params.keywords = '';
		},
	},
}
</script>

<style lang="scss">
.main-area{
	padding: 0 30rpx 0 0;
}
	
.search-area{
	background-color: #FFFFFF;
	padding: 20rpx 20rpx 10rpx 20rpx;
}	

.grid-box{
	padding: 10rpx 10rpx 10rpx 10rpx;
	.grid-image {
		width: 60rpx;
		height: 60rpx;
	}
	.grid-text {
		padding: 0 0 20rpx 0;
		letter-spacing: 2rpx;
		font-size: 20rpx;
		font-weight: bold;
	}
	.grid-cond{
		margin-bottom: 20rpx;
		font-size: 26rpx;
		text-align: center;
		background: #f2f2f2;
		padding: 10rpx 0 0 0;
		width: 150rpx;
		height: 50rpx;
	}
}
.price-box{
	height: 200rpx;
}
.index-list-area{
	padding: 0 50rpx 0 0;
	position: relative;
	.index_item{
		padding: 20rpx 0 20rpx 20rpx;
		.index_item_img{
			height: 60rpx;
			width: 60rpx;
		}
		.index_item_brand{
			padding: 8rpx 0 0 50rpx;
			position: absolute;
		}
	}
}
</style>
<style lang="scss" scoped>
